@font-face {
  font-family: "Ubuntu";
  src: url("./assets/fonts/Ubuntu-Bold.ttf");
  font-weight: 700;
}
@font-face {
  font-family: "Ubuntu";
  src: url("./assets/fonts/Ubuntu-Medium.ttf");
  font-weight: 500;
}
@font-face {
  font-family: "Ubuntu";
  src: url("./assets/fonts/Ubuntu-Regular.ttf");
  font-weight: 400;
}
:root {
  --marine-blue: hsl(213, 96%, 18%);
  --purplish-blue: hsl(243, 100%, 62%);
  --pastel-blue: hsl(228, 100%, 84%);
  --light-blue: hsl(206, 94%, 87%);
  --strawberry-red: hsl(354, 84%, 57%);

  --cool-gray: hsl(231, 11%, 63%);
  --light-gray: hsl(229, 24%, 87%);
  --magnolia: hsl(217, 100%, 97%);
  --alabaster: hsl(231, 100%, 99%);
  --white: hsl(0, 0%, 100%);
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

body {
  font-family: "Ubuntu";
  height: 100vh;
  position: relative;
  display: grid;
  place-items: center;
  font-size: 16px;
  color: var(--marine-blue);
  background-color: var(--light-blue);
}
.app_container {
  display: flex;
  width: 100%;
  height: 100%;
  // @media (max-width: 375.99px) {
  //   flex-direction: column;
  //   justify-content: center;
  //   align-items: center;
  // }
}
.side-bar {
  background: url("./assets/images/bg-sidebar-desktop.svg") no-repeat;
  padding: 1.5rem 5rem 1.5rem 1rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  // @media (max-width: 375px) {
  //   background: url("./assets/images/bg-sidebar-mobile.svg") no-repeat;
  // }
  .step {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    text-align: start;
    gap: 1rem;
    .number {
      border-radius: 50%;
      width: 35px;
      height: 35px;
      display: flex;
      align-items: center;
      justify-content: center;
      border: solid 1px var(--pastel-blue);
      color: var(--white);
      font-size: 16px;
      font-weight: 500;
      &.active {
        background-color: var(--light-blue);
        color: var(--marine-blue);
      }
    }
  }
}
.app_container {
  background-color: var(--white);
  border-radius: 10px;
}
.step {
  font-weight: 500;
  color: var(--light-blue);
  text-transform: uppercase;
}
.step-name {
  font-weight: 500;
  color: var(--white);
  text-transform: uppercase;
}
.go-back-btn {
  font-weight: 700;
  color: var(--cool-gray);
  text-decoration: none;
  transition: all 0.2s ease;
  margin-top: 64px;

  &:hover {
    color: var(--marine-blue);
  }
}

.step-navigation {
  display: flex;
  width: 100%;

  justify-content: space-between;
  align-items: center;
}
.second_step_plan-select {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  margin-top: 1rem;
  margin-bottom: 1rem;
  height: 100%;
  gap: 1rem;

  .plan-select {
    display: flex;
    flex-direction: column;
    padding: 1rem;
    height: 100%;
    width: 33%;
    cursor: pointer;
    border: solid 1px var(--light-gray);
    border-radius: 10px;
    &:hover,
    &.active-plan {
      border: solid 1px var(--marine-blue);
    }

    justify-content: space-between;
    .plan-select-icon {
      margin-bottom: 2rem;
    }
    .plan-select-title {
      margin-bottom: 0.3rem;
      color: var(--marine-blue);
      font-weight: 500;
    }
    .plan-select-cost {
      color: var(--cool-gray);
      font-size: 14px;
    }
  }
}
.second_step_period-select {
  display: flex;
  margin: 1rem 1rem 4rem 1rem;
  padding: 0 10rem;
  width: 100%;
  justify-content: flex-end;
  gap: 1rem;
  input.year-input:checked + label {
    color: var(--marine-blue);
  }

  span {
    width: 40px;
    height: 20px;
    border-radius: 15px;
    background-color: var(--marine-blue);
    position: relative;
    &::after {
      content: "";
      height: 14px;
      width: 14px;
      border-radius: 50%;
      background-color: var(--white);
      position: absolute;
      left: 3px;
      top: 3px;
      transition: all 0.2s ease-in-out;
    }
    &.active-span:after {
      left: 23px;
    }
  }

  label {
    cursor: pointer;
    color: var(--cool-gray);
  }
}

.second_step_period-select input {
  cursor: pointer;
  opacity: 0;
}
.third-step-add-pick {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 100%;
}
.add-ons {
  cursor: pointer;
  width: 100%;
  height: 100%;
  border: solid 1px var(--purplish-blue);
  padding: 1rem;
  border-radius: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  input {
    cursor: pointer;
    margin-right: 1rem;
    width: 1rem;
    height: 1rem;
    font-size: 13px;
  }
  .add-ons-info {
    margin-right: 4rem;
    width: 100%;
    gap: 0.5rem;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    text-align: start;
    max-width: 220px;
    .add-ons-title {
      color: var(--marine-blue);
    }
    .add-ons-description {
      color: var(--cool-gray);
    }
  }
  .third-step-price {
    margin-left: 4rem;
    justify-self: flex-end;
    color: var(--purplish-blue);
  }
}

.step-content {
  display: flex;
  flex-direction: column;
  width: 750px;
  height: 565px;
  align-items: flex-start;
  text-align: start;
  padding: 2rem 4rem 1.5rem 5rem;

  .step-title {
    font-weight: 700;
    font-size: 32px;
    margin-bottom: 1rem;
  }
  .step-description {
    color: var(--cool-gray);
    margin-bottom: 1rem;
  }
  .first_step-form {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-bottom: 1rem;
    input {
      margin-bottom: 1rem;
      padding: 0.75rem 0.5rem;
      border-radius: 10px;
      border: solid 1px var(--light-gray);
      width: 100%;
      color: var(--marine-blue);
      cursor: pointer;
      &:focus {
        outline: var(--pastel-blue) solid 1px;
      }
      &.err {
        outline: var(--strawberry-red) solid 1px;
      }
    }
    label {
      margin-bottom: 1rem;
      font-weight: 500;
    }
  }
  .step-btn {
    background-color: var(--marine-blue);
    color: var(--white);
    font-weight: 700;
    text-decoration: none;
    padding: 15px 25px;
    border-radius: 10px;
    transition: all 0.2s ease-in-out;
    align-self: flex-end;
    margin-top: 4rem;
    &:hover {
      background-color: var(--purplish-blue);
    }
  }
}
.input-error {
  font-size: 12px;
  color: var(--strawberry-red);
  position: absolute;
  bottom: 0;
  right: 0;
}
.input-group {
  position: relative;
  display: flex;
  flex-direction: column;
}
.finish {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1.5rem;
  img {
    max-width: 4rem;
  }
}
.finish {
  p {
    max-width: 450px;
    text-align: center;
  }
}
.form_fourth_step {
  gap: 1.5rem;
}
.fourth-step-info {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  flex: 0 1 100%;
  width: 100%;
  padding: 0 3rem 0 1.5rem;
  .plan-selected {
    display: flex;
    width: 100%;
    justify-content: space-between;
  }
  p {
    display: flex;
    width: 100%;
    justify-content: space-between;
    color: var(--marine-blue);
    span {
      color: var(--cool-gray);
    }
  }
}
.change {
  color: var(--light-gray);
  transition: all 0.2s easy;
  margin-top: 0.5rem;
  &:hover {
    color: var(--marine-blue);
  }
}
.plan-cost {
  color: var(--marine-blue);
  font-weight: 700;
}
.sum-cost {
  font-size: 20px;
  color: var(--purplish-blue) !important;
}
